*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: cursive;
}

.navbar{
    display: flex;
    background: #0d6efd;
    height: 90px;
    flex-wrap: nowrap;
    justify-content: space-evenly;
    align-content: center;
    align-items: center;
    a{
        color: #fff;
    text-decoration: none;
    font-size: 20px;
    }
}
.heroSection{
    display: flex;
    flex-wrap: wrap;
    align-content: center;
    justify-content: center;
    align-items: center;
    gap: 10px;
    .heroWrapper{
        width: 300px;
        height: 400px;
        flex-wrap: wrap;
        text-align: center;
        // display: flex;
        img{
            width: 250px;
            height: 250px;
        }
        p{
            font-size: 20px;
            color: #0d6efd;
            text-align: center;
        }
    }
}

.footer{
    display: flex;
    background: #0d6efd;
    height: 90px;
    flex-wrap: nowrap;
    justify-content: space-evenly;
    align-content: center;
    align-items: center;
    position: fixed;
    bottom: 0;
    width: 100%;
    color: #fff;

}